
<template>
  <div class="container">
    <div :class="['info',{'hidden':blackScreen}]">
      <div :class="['info-input',{'hidden':this.showHint}]">
        <div class="name">
          <span>Name:</span>
          <input type="text" v-model="name" />
        </div>
        <div class="num">
          <span>Exp: &nbsp;&nbsp;&nbsp;</span>
          <input type="text" v-model="exp"/>
        </div>
        <div class="num">
          <span>No:  &nbsp;&nbsp;&nbsp;</span>
          <input type="text" v-model="num"/>
        </div>
        <div class="begin">
          <div class="button" @click="beginTest">Start</div>
        </div>
      </div>
      <div :class="['hint',{'hidden': !showHint}]">
        即将进入实验阶段<br>
        Ready for experiment.
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      name: '',
      age: '18',
      num: '',
      exp: '',
      showHint: false,
      blackScreen: false
    }
  },
  mounted () {
    this.showHint = false
    this.blackScreen = false
  },
  methods: {
    beginTest () {
      if (this.name && this.exp && this.age && this.num && this.num < 7 && this.num > 0 && (this.num % 1 === 0)) {
        this.showHint = true
        setTimeout(() => {
          this.blackScreen = true
        }, 3000)
        setTimeout(() => {
          this.$router.push({path: '/image', query: {name: this.name.concat(this.exp), age: this.age, number: this.num}})
        }, 1000)
      } else {
        alert('姓名、年龄或序号错误！')
      }
    }
  }
}
</script>
<style lang="less" scoped>
.container {
  width: 100vw;
  height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  .info {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    background-color: rgba(244,239,225);
    color:#df6426;
    &.hidden {
      display: none;
    }
    .info-input {
      &.hidden {
        display: none;
      }
      .name {
        margin-bottom: 10px;
        font-weight: bold;
      }
      .age {
        font-weight: bold;
        margin-bottom: 10px;
      }
      .num {
        font-weight: bold;
        margin-bottom: 10px;
      }
      .begin {
        display: flex;
        justify-content: center;
        .button {
          padding: 5px;
          text-align: center;
          width: 80px;
          color: #ffffff;
          background-color: #df6426;
          border-radius: 4px;
        }
      }
    }
  }
  .hint {
    color: #df6426;
    &.hidden {
      display: none;
    }
  }
}
input {
  background-color: rgba(245,230,218);
  border: 1px solid rgba(232,200,178);
  padding: 5px;
  border-radius: 2px;
  margin-left: 10px;
  color: #a0806b;
}
</style>




